<!-- 장바구니 페이지 -->
<template>
    <!-- <div class="container"> -->
        <div v-for="(menu, mIndex) in menus" :key="mIndex" class="card" style="width: 400px;">
            <div class="card-body">
                <!-- <div class="container text-center"> -->
                    <div class="row">
                        <div class="col-8">
                            <p class="title" style="text-align: left;">{{ menu.fname }}<br>옵션 : 곱빼기 (+500)</p>
                        </div>
                        
                        <!-- 친구 / 금액 읿력 collapse 여는 버튼 --> <!-- id 부여해야함-->
                        <!-- <div class="col-2">
                            <button :id="'button-' + mIndex" class="btn btn-primary" type="button" data-bs-toggle="collapse" :data-bs-target="'#collapse-' + mIndex" aria-expanded="false" :aria-controls="'#collapse-' + mIndex">
                                <svg xmlns="http://www.w3.org/2000/svg" width="23" height="23" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
                                    <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6"/>
                                    <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5"/>
                                </svg>
                            </button>
                        </div> -->
                        <div class="col-1"><button @click="rmCard(mIndex)" type="button" class="btn-close" aria-label="Close"></button></div>
                    </div> <!-- div.row -->

                    <div class="row">
                        <div class="input-group mb-3" style="width: 250px;">
                            <!-- 수량 버튼 -->
                            <button class="btn btn-outline-secondary" @click="decrement(mIndex)" type="button">-</button>
                            <input type="number" :id="'button-' + mIndex" class="form-control" :value="menu.fcnt" aria-label="Example text with two button addons" disabled>
                            <button class="btn btn-outline-secondary" @click="increment(mIndex)" type="button">+</button>
                            <p> 최종 금액</p>
                        </div>
                    </div><!-- div.row -->

                <div class="collapse" :id="'collapse-' + mIndex">
                    <!-- 친구 / 금액 읿력 collapse, 장바구니속 메뉴들, 카드 인덱스 번호,  -->
                    <!-- <C_CLcardOpt :menusProp="menus" :mIndex="mIndex" /> -->
                </div>
    
            </div>      
        </div><!--card-->
  <!-- </div> container -->
</template>
<script>
import {mapGetters} from 'vuex'
const cardStore = 'cardStore'
// import C_CLcardOpt from "../client/C_CLcardOpt.vue"
    export default{
        data(){
            return{
                // value1 : 0,
                fCnt : 1,
                menus :[
                    // name (메뉴이름), data (입력받은 사람 / 금액), cnt (주문수량)
                    {fname : '짜장면', data:[], fcnt:1},
                    {fname : '짬뽕', data:[], fcnt:1},
                    {fname : '짜장면', data:[], fcnt:1}
                ]
            };
        },computed:{
            ...mapGetters(cardStore,[
                'GE_CART_LIST'
            ])
        },methods:{
            increment(mIndex){
                // const newCnt = this.menus[index].cnt;
                // this.$emit('cnt-change', newCnt);
                // alert(this.menus[index].cnt);
                this.menus[mIndex].fcnt++;
            },decrement(mIndex){
                if(this.menus[mIndex].fcnt > 1){
                    this.menus[mIndex].fcnt--;
                }
            },rmCard(mIndex){
                this.menus.splice(mIndex, 1);
            }
        },
        // components:{C_CLcardOpt},
        
    }
</script>